@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700;800;900&display=swap);
*{font-family:'Orbitron', sans-serif;font-size:20px;background-color:#72a079}.calc{display:grid;place-items:center}h1{text-align:center;margin-top:8vh;font-size:30px;font-weight:900;color:#bb371a;text-shadow:1px 1px #333}.container{top:50%;left:50%;position:absolute;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);margin:0 auto;min-width:256px;width:256px;text-align:center;border:2px solid #94987f;padding:10px;border-radius:3px;background:#fff8d9;box-shadow:1px 1px 0px #b4b89a,2px 2px 0px #b4b89a,3px 3px 0px #b4b89a,4px 4px 0px #b4b89a,5px 5px 0px #b4b89a,6px 6px 0px #b4b89a,7px 7px 0px #b4b89a,8px 8px 0px #b4b89a,9px 9px 0px #b4b89a,10px 10px 0px #b4b89a,11px 11px 0px #b4b89a,12px 12px 0px #94987f}.result{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center;min-height:70px;height:70px;min-width:247px;width:247px;background-color:#d5dbb3;border-radius:3px;border:2px solid #94987f;color:#bb371a;box-shadow:inset 3px 3px #b4b89a,inset 4px 4px #94987f;margin-left:3px;margin-top:2px}.keypad{display:grid;grid-template-columns:repeat(4, 1fr);grid-auto-rows:60px;background:#fff8d9;margin-top:5px;max-width:256px;width:256px}.clear{grid-column:1/3}.equals{grid-column:3/5}button{margin:4px;border:2px solid #b4b89a;border-radius:4px;box-shadow:3px 3px #d5dbb3,4px 4px #b4b89a;background-color:#d5dbb3;color:#bb371a;cursor:pointer;transition:box-shadow 0.1s ease-in-out;outline:0;text-shadow:1px 1px #94987f}button:active{box-shadow:0px 0px;outline:none}button:hover{background-color:#fff8d9}.face{background-color:#d5dbb3;font-size:28px;padding-right:8px;padding-top:8px;text-shadow:1px 1px #b4b89a}.button:focus{outline:0}.highlight{background-color:#eba83a;border:2px solid #b5802c;box-shadow:3px 3px #eba83a,4px 4px #b5802c;text-shadow:1px 1px #b5802c}

